'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Label } from '@/components/ui/label'
import { Separator } from '@/components/ui/separator'
import { Phone, Mail, MapPin, Clock, Send, CheckCircle } from 'lucide-react'

export default function ContactPage() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    subject: '',
    message: '',
    preferredContact: ''
  })
  const [isSubmitted, setIsSubmitted] = useState(false)

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    // Here you would typically send the form data to your backend
    console.log('Form submitted:', formData)
    setIsSubmitted(true)
    
    // Reset form after 3 seconds
    setTimeout(() => {
      setIsSubmitted(false)
      setFormData({
        name: '',
        email: '',
        phone: '',
        subject: '',
        message: '',
        preferredContact: ''
      })
    }, 3000)
  }

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }))
  }

  const contactInfo = [
    {
      icon: <Phone className="h-6 w-6" />,
      title: 'Telefon',
      details: ['+387 60 354 3153'],
      description: 'Pozovite nas radnim danima od 8:00 do 17:00'
    },
    {
      icon: <Mail className="h-6 w-6" />,
      title: 'Email',
      details: ['info@alizino.com'],
      description: 'Odgovaramo na sve upite u roku od 24 sata'
    },
    {
      icon: <MapPin className="h-6 w-6" />,
      title: 'Adresa',
      details: ['Bukvik Donji bb', '76100 Brčko', 'Bosna i Hercegovina'],
      description: 'Posjetite naš showroom i radionicu'
    },
    {
      icon: <Clock className="h-6 w-6" />,
      title: 'Radno vrijeme',
      details: ['Pon - Pet: 8:00 - 17:00', 'Sub: 9:00 - 14:00', 'Ned: Zatvoreno'],
      description: 'Dostupni smo i za termine van radnog vremena'
    }
  ]

  const subjects = [
    'Opći upit',
    'Zahtjev za ponudu',
    'Naručivanje proizvoda',
    'Proizvod po mjeri',
    'Tehnička podrška',
    'Reklamacija',
    'Partnerstvo',
    'Ostalo'
  ]

  return (
    <div className="min-h-screen py-8">
      <div className="container mx-auto px-4">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold mb-4" style={{ fontFamily: 'Playfair Display, serif' }}>
            Kontaktirajte nas
          </h1>
          <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
            Imate pitanja ili trebate pomoć? Tu smo za vas! Kontaktirajte nas bilo kojim od načina ispod.
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Contact Form */}
          <div className="lg:col-span-2">
            <Card>
              <CardHeader>
                <CardTitle className="text-2xl">Pošaljite nam poruku</CardTitle>
              </CardHeader>
              <CardContent>
                {isSubmitted ? (
                  <div className="text-center py-8">
                    <CheckCircle className="h-16 w-16 text-green-500 mx-auto mb-4" />
                    <h3 className="text-xl font-semibold mb-2">Poruka je uspješno poslana!</h3>
                    <p className="text-muted-foreground">
                      Hvala vam na upitu. Odgovorit ćemo vam u najkraćem mogućem roku.
                    </p>
                  </div>
                ) : (
                  <form onSubmit={handleSubmit} className="space-y-6">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <Label htmlFor="name">Ime i prezime *</Label>
                        <Input
                          id="name"
                          value={formData.name}
                          onChange={(e) => handleInputChange('name', e.target.value)}
                          placeholder="Vaše ime i prezime"
                          required
                        />
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="email">Email adresa *</Label>
                        <Input
                          id="email"
                          type="email"
                          value={formData.email}
                          onChange={(e) => handleInputChange('email', e.target.value)}
                          placeholder="vasa@email.com"
                          required
                        />
                      </div>
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <Label htmlFor="phone">Broj telefona</Label>
                        <Input
                          id="phone"
                          value={formData.phone}
                          onChange={(e) => handleInputChange('phone', e.target.value)}
                          placeholder="+387 XX XXX XXX"
                        />
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="preferredContact">Preferirani način kontakta</Label>
                        <Select value={formData.preferredContact} onValueChange={(value) => handleInputChange('preferredContact', value)}>
                          <SelectTrigger>
                            <SelectValue placeholder="Odaberite način" />
                          </SelectTrigger>
                          <SelectContent>
                            <SelectItem value="email">Email</SelectItem>
                            <SelectItem value="phone">Telefon</SelectItem>
                            <SelectItem value="both">Bilo koji</SelectItem>
                          </SelectContent>
                        </Select>
                      </div>
                    </div>

                    <div className="space-y-2">
                      <Label htmlFor="subject">Tema upita *</Label>
                      <Select value={formData.subject} onValueChange={(value) => handleInputChange('subject', value)} required>
                        <SelectTrigger>
                          <SelectValue placeholder="Odaberite temu" />
                        </SelectTrigger>
                        <SelectContent>
                          {subjects.map((subject) => (
                            <SelectItem key={subject} value={subject}>
                              {subject}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>

                    <div className="space-y-2">
                      <Label htmlFor="message">Poruka *</Label>
                      <Textarea
                        id="message"
                        value={formData.message}
                        onChange={(e) => handleInputChange('message', e.target.value)}
                        placeholder="Opišite vaš upit ili potrebe..."
                        rows={6}
                        required
                      />
                    </div>

                    <Button type="submit" size="lg" className="w-full">
                      <Send className="mr-2 h-4 w-4" />
                      Pošaljite poruku
                    </Button>
                  </form>
                )}
              </CardContent>
            </Card>
          </div>

          {/* Contact Information */}
          <div className="space-y-6">
            {contactInfo.map((info, index) => (
              <Card key={index}>
                <CardContent className="p-6">
                  <div className="flex items-start space-x-4">
                    <div className="flex-shrink-0 p-2 bg-primary/10 rounded-lg text-primary">
                      {info.icon}
                    </div>
                    <div className="flex-grow">
                      <h3 className="font-semibold text-lg mb-2">{info.title}</h3>
                      <div className="space-y-1 mb-2">
                        {info.details.map((detail, idx) => (
                          <p key={idx} className="text-muted-foreground">
                            {detail}
                          </p>
                        ))}
                      </div>
                      <p className="text-sm text-muted-foreground">
                        {info.description}
                      </p>
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>

        {/* Map Section */}
        <div className="mt-12">
          <Card>
            <CardHeader>
              <CardTitle className="text-2xl">Naša lokacija</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                <div>
                  <h3 className="text-lg font-semibold mb-4">Showroom i radionica</h3>
                  <div className="space-y-4">
                    <div className="flex items-start space-x-3">
                      <MapPin className="h-5 w-5 text-primary mt-0.5" />
                      <div>
                        <p className="font-medium">Adresa:</p>
                        <p className="text-muted-foreground">
                          Bukvik Donji bb<br />
                          76100 Brčko<br />
                          Bosna i Hercegovina
                        </p>
                      </div>
                    </div>
                    <div className="flex items-start space-x-3">
                      <Clock className="h-5 w-5 text-primary mt-0.5" />
                      <div>
                        <p className="font-medium">Radno vrijeme:</p>
                        <p className="text-muted-foreground">
                          Ponedjeljak - Petak: 8:00 - 17:00<br />
                          Subota: 9:00 - 14:00<br />
                          Nedjelja: Zatvoreno
                        </p>
                      </div>
                    </div>
                  </div>
                  
                  <Separator className="my-6" />
                  
                  <div>
                    <h4 className="font-semibold mb-2">Kako do nas:</h4>
                    <ul className="text-muted-foreground space-y-1 text-sm">
                      <li>• Javni transport: Autobuska stanica Brčko</li>
                      <li>• Parking: Dostupan besplatan parking ispred objekta</li>
                      <li>• Iz centra Brčka: 10 minuta vožnje</li>
                    </ul>
                  </div>
                </div>
                
                {/* Placeholder for map - in real implementation, you would use Google Maps or similar */}
                <div className="bg-muted rounded-lg h-64 lg:h-80 flex items-center justify-center">
                  <div className="text-center">
                    <MapPin className="h-12 w-12 text-muted-foreground mx-auto mb-2" />
                    <p className="text-muted-foreground">
                      Interaktivna mapa<br />
                      <span className="text-sm">(Google Maps integracija)</span>
                    </p>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* FAQ Section */}
        <div className="mt-12">
          <Card>
            <CardHeader>
              <CardTitle className="text-2xl">Često postavljana pitanja</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <h4 className="font-semibold mb-2">Koliko traje izrada namještaja po mjeri?</h4>
                  <p className="text-muted-foreground text-sm mb-4">
                    Vrijeme izrade zavisi od složenosti projekta, obično između 2-6 sedmica.
                  </p>
                  
                  <h4 className="font-semibold mb-2">Da li nudite garanciju?</h4>
                  <p className="text-muted-foreground text-sm mb-4">
                    Da, nudimo garanciju od 2-10 godina ovisno o proizvodu i materijalima.
                  </p>
                </div>
                <div>
                  <h4 className="font-semibold mb-2">Da li dostavljate van Brčka?</h4>
                  <p className="text-muted-foreground text-sm mb-4">
                    Da, dostavljamo u cijelu Bosnu i Hercegovinu, kao i susjedne zemlje.
                  </p>
                  
                  <h4 className="font-semibold mb-2">Mogu li vidjeti proizvod prije kupovine?</h4>
                  <p className="text-muted-foreground text-sm mb-4">
                    Apsolutno! Pozivamo vas da posjetite naš showroom ili zatražite uzorke materijala.
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
